export function pushToDataLayer(
  data: {
    event:
      | "sign_up"
      | "purchase"
      | "upgrade"
      | "chatbot_created"
      | "chatbot_launched";
  } & Record<string, string | number>
) {
  try {
    window.dataLayer.push(data);
  } catch (error) {
    console.log(error);
  }
}
